import moment from 'moment'

import BetEntry from '../bet-entry'
import PlayerSearch from '../player-search'

export default {
  name: 'BetBox',

  components: {
    BetEntry,
    PlayerSearch
  },

  props: [
    'draw'
  ],

  data() {
    return {
      player: {
        player_id: null,
        contact_no: null,
        combinations: []
      },
      showDialog: false,
      inProgress: false
    }
  },

  computed: {
    data() {
      return [
        {
          "draw_id": 3,
          "combination": "1-3-5",
          "bet": 10,
          "winningprize": 24500,
          "isramble": "S"
        }
      ]
    },

    dialog() {
      return this.draw ? {
        title: this.draw.name,
        date: moment().format('MMMM DD, YYYY')
      } : null
    }
  },

  methods: {
    handleClose(done) {
      this.player = {
        player_id: null,
        contact_no: null,
        combinations: []
      }

      if (typeof done == 'function') {
        done()
      }
    },

    openDialog() {
      this.showDialog = true
    },

    closeDialog() {
      this.showDialog = false
      this.handleClose()
    },
  }
}



